import React from "react"
type Props = {
  tags: string[]
}

const Tags = ({ tags }: Props) => {
  return (
    <ul className="flex flex-wrap gap-y-1 gap-x-2">
      {tags.map(tagName => (
        <li key={tagName} className="inline-block">
          <span className="text-xs inline-block py-1 px-2.5 leading-none text-center whitespace-nowrap align-baseline font-bold bg-[color:var(--color-blue)]  text-[color:var(--color-white)] rounded-full">
            {tagName}
          </span>
        </li>
      ))}
    </ul>
  )
}
export default Tags
